export default {
  mounted() {
    // 高亮底部 菜单
    this.$store.commit('layout/setPageActive',1);
  },
  methods : {
    // 左滑动
    handleLeft(e) {
      this.$router.push('/wode')
    },
    // 右滑动
    handleRight(e) {
      this.$router.push('/index')
    },
  },
}



